import React, { useState, ReactElement } from 'react';
import { useNavigate } from 'react-router-dom';
import Parse from 'parse';
import style from './UserRegistration.module.css';
import Notification from '../Notification/Notification';

function UserRegistration(): ReactElement {
  const navigate = useNavigate();
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [showNotification, setShowNotification] = useState(false);
  const [notificationType, setNotificationType] = useState('');
  const [notificationMsg, setNotificationMsg] = useState('');

  const doUserRegistration = async (): Promise<boolean> => {
    setShowNotification(false);
    const usernameValue: string = username;
    const passwordValue: string = password;
    try {
      await Parse.User.signUp(usernameValue, passwordValue, '');
      setNotificationType('success');
      setNotificationMsg('  !');
      setShowNotification(true);
      setPassword('');
      setUsername('');
      // TODO    Notification
      navigate('/login');
      return true;
    } catch (error: any) {
      setNotificationType('error');
      setNotificationMsg(` : ${error}`);
      setShowNotification(true);
      setUsername('');
      setPassword('');
      return false;
    }
  };

  return (
    <div className={style.wrapper}>
      <div className={style.title}>  </div>
      <div>
        <input
          value={username}
          onChange={(event: any) => setUsername(event.target.value)}
          placeholder=" "
          type="username"
          className={style.inputField}
        />
      </div>
      <div>
        <input
          value={password}
          onChange={(event: any) => setPassword(event.target.value)}
          placeholder=""
          type="password"
          className={style.inputField}
        />
      </div>
      <div>
        <button
          type="button"
          className={style.registerBtn}
          onClick={() => doUserRegistration()}
        >
          
        </button>
        {showNotification && (
          <Notification
            type={notificationType}
            message={notificationMsg}
            duration={5000}
          />
        )}
      </div>
    </div>
  );
}

export default UserRegistration;
