import React, { useState, ReactElement, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import Parse from 'parse';
import style from './UserLogIn.module.css';
import Notification from '../Notification/Notification';

function UserLogin(): ReactElement {
  const navigate = useNavigate();
  const toRegistrationPage = (event: any): void => {
    event.preventDefault();
    navigate('/sign-up');
  };

  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [currentUser, setCurrentUser] = useState<Parse.User | null>(null);

  const [showNotification, setShowNotification] = useState(false);
  const [notificationType, setNotificationType] = useState('');
  const [notificationMsg, setNotificationMsg] = useState('');

  const getCurrentUser = async (): Promise<Parse.User | null> => {
    const user: Parse.User | null = await Parse.User.currentAsync();
    setCurrentUser(user);
    return currentUser;
  };

  useEffect(() => {
    getCurrentUser();
  }, [currentUser]);

  const doUserLogIn = async (): Promise<boolean> => {
    setShowNotification(false);
    const usernameValue: string = username;
    const passwordValue: string = password;
    try {
      await Parse.User.logIn(usernameValue, passwordValue);
      setNotificationType('success');
      setNotificationMsg('     ');
      setShowNotification(true);
      setUsername('');
      setPassword('');
      getCurrentUser();
      return true;
    } catch (error: any) {
      setNotificationType('error');
      setNotificationMsg(`: ${error}`);
      setShowNotification(true);
      setUsername('');
      setPassword('');
      return false;
    }
  };

  const doUserLogOut = async (): Promise<boolean> => {
    try {
      await Parse.User.logOut();
      const user: Parse.User | null = await Parse.User.currentAsync();
      if (user === null) {
        setNotificationType('default');
        setNotificationMsg('   ');
        setShowNotification(true);
      }
      getCurrentUser();
      return true;
    } catch (error: any) {
      setNotificationType('error');
      setNotificationMsg(`: ${error.message}`);
      setShowNotification(true);
      return false;
    }
  };

  return (
    <div>
      {currentUser === null && (
        <div className={style.wrapper}>
          <div className={style.title}>   </div>
          <div>
            <input
              value={username}
              onChange={(event: any) => setUsername(event.target.value)}
              placeholder=" "
              type="username"
              className={style.inputField}
            />
          </div>
          <div>
            <input
              value={password}
              onChange={(event: any) => setPassword(event.target.value)}
              placeholder=""
              type="password"
              className={style.inputField}
            />
          </div>
          <div>
            <button
              type="button"
              className={style.registerBtn}
              onClick={() => doUserLogIn()}
            >
              
            </button>
            {showNotification && (
              <Notification
                type={notificationType}
                message={notificationMsg}
                duration={5000}
              />
            )}
            <p className={style.hint}>
               ?{' '}
              <a className="form__link" href="#" onClick={toRegistrationPage}>
                
              </a>
            </p>
          </div>
        </div>
      )}
      {currentUser !== null && (
        <div className={style.wrapper}>
          <div className={style.title}>
            {`Hello ${currentUser.get('username')}!`}
          </div>
          <div>
            <button
              type="button"
              className={style.registerBtn}
              onClick={() => doUserLogOut()}
            >
              
            </button>
            {showNotification && (
              <Notification
                type={notificationType}
                message={notificationMsg}
                duration={5000}
              />
            )}
          </div>
        </div>
      )}
    </div>
  );
}

export default UserLogin;
